.journal__main-content{
    display: flex;
    width: 100vw;
}

.journal__sidebar{
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
    @media screen and (max-width:800px) {
        width: 200px;
    }
}

.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;

    h3{
        font-weight: lighter;
    }
}

.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 30px;
    width: 100%;

    transition:color .3s ease;

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries{
    flex: 1 1 auto;
    margin: top 30px;
    overflow-y: scroll;
}

.journal__entry{
    background-color: white;
    border-radius: 4px;
    color:$dark-grey;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow: hidden;
}

.journal__entry-picture{
    width: 100px;
}

.journal__entry-body{
    padding: 10px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.journal__entry-title{
    font-size: 16px;
    font-weight: bold;
}
.journal__entry-content{
    font-size: 12px;
}
.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    span{
        font-size: 12px;
    }
}